body {
  font-family: Rubik, sans-serif;
  font-weight: 500;
}

.header {
  background: url("pattern-bg-desktop.ffa90831.png") center / cover no-repeat;
  flex-direction: column;
  align-items: center;
  height: 250px;
  display: flex;
  position: relative;
}

.h1 {
  color: #fff;
  margin-top: 20px;
  font-size: 24px;
  font-weight: 700;
}

.search__bar {
  background-color: #fff;
  border-radius: 8px;
  align-items: center;
  width: 450px;
  margin-top: 12px;
  display: flex;
}

.input {
  width: 100%;
  height: 50px;
  margin-left: 12px;
  padding-block: 12px;
  font-size: 18px;
  font-weight: 400;
}

.input::placeholder {
  color: #949494;
  font-size: 18px;
}

.button {
  background: #000;
  border-radius: 0 8px 8px 0;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 100%;
  margin-left: auto;
  margin-right: 0;
  display: flex;

  &:hover {
    background: #0f0f0f;
  }
}

.output__container {
  z-index: 1000;
  background-color: pink;
  border-radius: 12px;
  width: 800px;
  margin-top: 180px;
  padding-block: 24px;
  display: flex;
  position: absolute;
}

.info-block {
  border-right: 1px solid #949494;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 200px;
  padding-inline: 12px;
  display: flex;
}

.clear-border {
  border-right: 0;
}

.info-header {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #949494;
  font-size: 12px;
}

.info {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}

.map__container {
  background-color: #949494;
  height: 1000px;
}

@media (width <= 820px) {
  .header {
    background: url("pattern-bg-mobile.ff090957.png") center / cover no-repeat;
    height: 340px;
  }

  .h1 {
    margin-top: 30px;
  }

  .search__bar {
    width: 350px;
    margin-top: 25px;
  }

  .input, .input::placeholder {
    font-size: 16px;
  }

  .output__container {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    width: 350px;
    margin-top: 160px;
    padding-block: 24px;
  }

  .info-block {
    border-right: 0;
  }
}
